import { isDate } from 'date-fns'
import parse from 'date-fns/parse'
import format from 'date-fns/format'
import getHours from 'date-fns/getHours'
import getMinutes from 'date-fns/getMinutes'
import getSeconds from 'date-fns/getSeconds'
import setHours from 'date-fns/setHours'
import setMilliseconds from 'date-fns/setMilliseconds'
import setMinutes from 'date-fns/setMinutes'
import setSeconds from 'date-fns/setSeconds'

export const formatDate = 'dd.MM.yyyy'
export const formatTime = 'HH:mm'
export const formatDateTime = 'dd.MM.yyyy HH:mm'

export function isCurrentDate(date) {
  const today = setHours(
    setMinutes(setSeconds(setMilliseconds(new Date(), 0), 0), 0),
    0
  )
  const selectedDate = setHours(
    setMinutes(setSeconds(setMilliseconds(date, 0), 0), 0),
    0
  )
  return selectedDate.getTime() === today.getTime()
}

export function getTime(date) {
  const h = addZeroToTime(getHours(date))
  const m = addZeroToTime(getMinutes(date))
  const s = addZeroToTime(getSeconds(date))
  return `${h}:${m}:${s}`
}

export function addZeroToTime(time) {
  return ('0' + time).slice(-2)
}

/** Возвращает дату вида "24.08.2022 10:15" */
export function getDateTime(date) {
  return format(date, formatDateTime)
}

/** Возвращает дату вида "24.08.2022" */
export function getDateOnly(date) {
  try {
    return format(date, formatDate)
  } catch (error) {
    return ''
  }
}

/**
 * парсит строку и получает дату с типом Date
 * @param date {string}
 * @return {Date}
 */
export function getParseDate(date) {
  return parse(date, formatDate, new Date)
}

export function stringifyIfIsDate(date) {
  if (isDate(date)) {
    return getDateOnly(date)
  } else {
    return date
  }
}

export function tomorrow() {
  const today = new Date()
  const tomorrow = new Date(today)
  tomorrow.setDate(tomorrow.getDate() + 1)
  return tomorrow
}
