import i18n from '../../../plugins/i18n'
import { FormStore } from '../FormStore'
import { formatDate } from '../../date'

/**
 * @param error {string|{errorMessageKey: string, params: Object}}
 * @return {string}
 */
export function getErrorMessage(error) {
  if (!error) return ''
  if (typeof error === 'string') {
    return i18n.t(`error.${error}`)
  } else {
    return i18n.t(
      `error.${error.errorMessageKey}`,
      error.params
    )
  }
}
/**
 * @param formStore {FormStore}
 * @param keyName {string} */
export const formStoreAdaptersInput = (formStore, keyName) => {
  return {
    value: formStore?.values[keyName] || '',
    /** @param e {React.ChangeEvent<HTMLInputElement>} */
    onChange: (e) => {
      formStore?.changeValue(e?.target?.value || '', keyName)
    },
    /** @type {string} */
    messageError: getErrorMessage(formStore.errors[keyName]),
    isInvalid: !!formStore?.errors[keyName],
    onClear: () => formStore.resetByKey(keyName),
    hideClearButton: false,
    onBlur: () => formStore.validateByKey(keyName),
  }
}
/**
 * @param formStore {FormStore}
 * @param keyName {string} */
export const formStoreAdaptersInputOnlyNumber = (formStore, keyName) => {
  return {
    value: formStore?.values[keyName] || '',
    /** @param e {React.ChangeEvent<HTMLInputElement>} */
    onChange: (e) => {
      const value = (e.target.value || '').replace(/\D/g,'')
      formStore?.changeValue(value, keyName)
    },
    /** @type {string} */
    messageError: getErrorMessage(formStore.errors[keyName]),
    isInvalid: !!formStore?.errors[keyName],
    onClear: () => formStore.resetByKey(keyName),
    hideClearButton: false,
    onBlur: () => formStore.validateByKey(keyName),
  }
}
/**
 * @param formStore {FormStore}
 * @param keyName {string} */
export const formStoreAdaptersInputINN = (formStore, keyName) => {
  return {
    value: formStore?.values[keyName] || '',
    /** @param e {React.ChangeEvent<HTMLInputElement>} */
    onChange: (e) => {
      const value = (e.target.value || '').replace(/\D/g,'')
      formStore?.changeValue(value, keyName)
    },
    /** @type {string} */
    messageError: getErrorMessage(formStore.errors[keyName]),
    isInvalid: !!formStore?.errors[keyName],
    onClear: () => formStore.resetByKey(keyName),
    hideClearButton: false,
    onBlur: () => formStore.validateByKey(keyName),
  }
}
/**
 * @param formStore {FormStore}
 * @param keyName {string} */
export const formStoreAdaptersNumberFormat = (formStore, keyName) => {
  return {
    value: formStore?.values[keyName] || '',
    onValueChange: (data) => {
      formStore?.changeValue(data?.value || '', keyName)
    },
    /** @type {string} */
    messageError: getErrorMessage(formStore.errors[keyName]),
    isInvalid: !!formStore?.errors[keyName],
    onClear: () => formStore.resetByKey(keyName),
    hideClearButton: false,
    onBlur: () => formStore.validateByKey(keyName),
    thousandSeparator: ' ',
    decimalSeparator: ',',
    decimalScale: 2,
    isNumericString: true,
  }
}

/**
 * @param formStore {FormStore}
 * @param keyName {string} */
export const formStoreAdaptersDatePicker = (formStore, keyName) => {
  return {
    value: formStore.values[keyName]
      ? new Date(formStore.values[keyName])
      : null,
    /** @param value {Date} */
    onChange: (value) => formStore.changeValue(value || '', keyName),
    inputProps: {
      /** @type {string} */
      messageError: getErrorMessage(formStore.errors[keyName]),
      isInvalid: !!formStore.errors[keyName],
    },
    isInvalid: !!formStore.errors[keyName],
    messageError: getErrorMessage(formStore.errors[keyName]),
    format: formatDate,
  }
}

/**
 * @param formStore {FormStore}
 * @param keyName {string}
 * @param variants {Object<string,any>[]}
 * @param findKey {string}
 * @returns {{onChange: (function(*): void), selectedItem: (*|null), messageError: (string), isInvalid: boolean, items}}
 */
export const formStoreAdaptersSelectBox = ({
  formStore,
  keyName,
  variants,
  findKey = 'id',
}) => {
  return {
    selectedItem:
      variants.find(
        (i) => i[findKey] === formStore?.values[keyName]
      ) || null,
    onChange: (item) => formStore.changeValue(item[findKey], keyName),
    isInvalid: !!formStore.errors[keyName],
    messageError: getErrorMessage(formStore.errors[keyName]),
    items: variants,
  }
}

/**
 * @param formStore {FormStore}
 * @param keyName {string}*/
export const formStoreAdaptersSwitcher = (formStore, keyName) => {
  return {
    checked: !!formStore.values[keyName],
    onChange: () =>
      formStore.changeValue(!formStore.values[keyName], keyName),
  }
}

export default {
  input: formStoreAdaptersInput,
  inn: formStoreAdaptersInputINN,
  number: formStoreAdaptersInputOnlyNumber,
  numberFormat: formStoreAdaptersNumberFormat,
  datePicker: formStoreAdaptersDatePicker,
  selectBox: formStoreAdaptersSelectBox,
  switcher: formStoreAdaptersSwitcher,
}
