import pack from '../../package.json'

export const orderBaseUrl = window.orderBaseUrl || window.urlApi
export const csrfToken = getMeta('csrf-token') || window.CSRF_TOKEN
export const yandexApiKey = window.YANDEX_API_KEY
export const sentryDnsKey = window.SENTRY_DNS_KEY
export const updateAutoSaveOrderDraft =
  parseFloat(window.UPDATE_AUTO_SAVE_ORDER_DRAFT) || 5
//Приходит из настроек webpack смотреть webpack.common new webpack.DefinePlugin({...})
export const production =
  typeof window.Production === 'boolean' ? window.Production : true
//OfficeInfo и IsGuest приходят с пребека и доступны глобально в window. Будем уходить от этого когда перейдем на чистый реакт
export const officeInfo = window.OfficeInfo || {}
export const isGuest = window.IsGuest

export const appVersion = pack.version

window.appVersion = appVersion

export const documentLang = getLang()

export const layoutRootClassName = 'lk-wrapper'

function getMeta(metaName) {
  const metas = document.getElementsByTagName('meta')

  for (let i = 0; i < metas.length; i++) {
    if (metas[i].getAttribute('name') === metaName) {
      return metas[i].getAttribute('content')
    }
  }

  return ''
}

function getLang() {
  return document.documentElement.lang
}

