import i18n from 'i18next'
import { initReactI18next } from 'react-i18next'
import en from '../assets/locales/en-US/translation.json'
import ru from '../assets/locales/ru-RU/translation.json'
import zh from '../assets/locales/zh-CN/translation.json'
import { production } from '../constants/globals.constant'

i18n.use(initReactI18next).init({
  debug: !production,
  lng: 'ru-RU',
  resources: {
    'ru-RU': { translation: ru },
    'en-US': { translation: en },
    'zh-CN': { translation: zh },
  },
})
export default i18n
