import {
  SegmentRadio,
  SelectBox,
  SelectionFromButtons,
} from '@cdek/react-uikit'

import { observer } from 'mobx-react-lite'
import PropTypes from 'prop-types'
import React from 'react'
import { useMediaQuery } from 'react-responsive'

const SelectBoxOrSelectionButtons = (props) => {
  const isMobile = useMediaQuery({
    query: `(max-width: ${props.maxWidth || 600}px)`,
  })

  return isMobile ? (
    <SelectBox {...props} />
  ) : (
    <SelectionFromButtons
      activeValue={
        props.items.find((i) => i === props.selectedItem)?.value
      }
      onChange={(value) => {
        props.onChange({ value })
      }}
    >
      {props.items?.map((i, idx) => (
        <SegmentRadio key={idx} value={i.value}>
          {String(i.name).toUpperCase()}
        </SegmentRadio>
      ))}
    </SelectionFromButtons>
  )
}

SelectBoxOrSelectionButtons.propTypes = {
  onChange: PropTypes.func,
  maxWidth: PropTypes.number,
  selectedItem: PropTypes.shape({}),
  items: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      value: PropTypes.string,
    })
  ),
}
SelectBoxOrSelectionButtons.displayName =
  'SelectBoxOrSelectionButtons'
export default observer(SelectBoxOrSelectionButtons)
