import { Input, Switcher, Typography } from '@cdek/react-uikit'
import Button, {
  ButtonSize,
  ButtonStyle,
} from '@cdek/react-uikit/lib/esm/components/Button/Button'

import { observer } from 'mobx-react-lite'
import PropTypes from 'prop-types'
import React from 'react'
import { useTranslation } from 'react-i18next'
import formStoreAdapters from '../../../../utils/FormStore/formStoreAdapters/formStoreAdapters'
import Paper from '../../../common/Paper'
import SelectBoxOrSelectionButtons from '../../../common/SelectBoxOrSelectionButtons/SelectBoxOrSelectionButtons'
import styles from './SntListFilter.module.scss'

const SntListFilter = (props) => {
  const { t } = useTranslation()

  const onPressEnter = (e) => {
    if (e.key === 'Enter' && props.getItems) {
      props.getItems()
    }
  }

  const typeOptions =
    props.typeVariants?.map((i) => ({
      name: t(`snt.transportTypes.${i}`).toUpperCase(),
      value: i,
    })) || []

  return (
    <div className={styles.block}>
      <div className={styles.header}>
        <Input
          className={styles.search}
          label={t(`snt.searchLabel`)}
          onKeyDown={onPressEnter}
          {...formStoreAdapters.input(props.form, 'search')}
        />
        <Switcher
          label={t(`snt.useFilter`)}
          checked={props.isActive}
          onChange={props.toggleActive}
        />
      </div>
      {props.isActive ? (
        <Paper className={styles.info}>
          <div className={styles.body}>
            <Typography type={'body-1'} className={styles.label}>
              {t(`snt.sender`)}
            </Typography>
            <Input
              label={t(`snt.city`)}
              onKeyDown={onPressEnter}
              {...formStoreAdapters.input(props.form, 'senderCity')}
            />
            <Input
              label={t(`snt.fio`)}
              onKeyDown={onPressEnter}
              {...formStoreAdapters.input(props.form, 'senderFio')}
            />
            <Input
              label={t(`snt.phone`)}
              onKeyDown={onPressEnter}
              {...formStoreAdapters.input(props.form, 'senderPhone')}
            />
            <Typography className={styles.label}>
              {t(`snt.receiver`)}
            </Typography>
            <Input
              label={t(`snt.city`)}
              onKeyDown={onPressEnter}
              {...formStoreAdapters.input(props.form, 'receiverCity')}
            />
            <Input
              label={t(`snt.fio`)}
              onKeyDown={onPressEnter}
              {...formStoreAdapters.input(props.form, 'receiverFio')}
            />
            <Input
              label={t(`snt.phone`)}
              onKeyDown={onPressEnter}
              {...formStoreAdapters.input(
                props.form,
                'receiverPhone'
              )}
            />
          </div>
          <div>
            <Button
              size={ButtonSize.Sm}
              styleMode={ButtonStyle.Secondary}
              className={styles.button}
            >
              {t(`snt.applyFilter`)}
            </Button>
          </div>
        </Paper>
      ) : null}
      <div>
        <Switcher
          label={t(`snt.readyToInput`)}
          {...formStoreAdapters.switcher(props.form, 'readyToInput')}
        />
      </div>
      <SelectBoxOrSelectionButtons
        items={typeOptions}
        onChange={(obj) => {
          props.setType(obj?.value)
        }}
        selectedItem={typeOptions.find(
          (i) => i.value === props.form.values.type
        )}
      />
    </div>
  )
}

SntListFilter.propTypes = {
  isActive: PropTypes.bool,
  toggleActive: PropTypes.func,
  getItems: PropTypes.func,
  setType: PropTypes.func,
  form: PropTypes.shape({
    values: PropTypes.shape({
      type: PropTypes.string,
    }),
  }),
  typeVariants: PropTypes.arrayOf(PropTypes.shape({})),
}
SntListFilter.displayName = 'SntListFilter'
export default observer(SntListFilter)
